import 'hammerjs'; // add Hammer to global namespace
import { loadPlugin } from './dynver';

export default function loadPlugins(picasso, version, callback) {
  if (!version) {
    if (window && window.picassoHammer) {
      picasso.use(window.picassoHammer);
    }
    if (window && window.picassoQ) {
      picasso.use(window.picassoQ);
    }
  } else {
    loadPlugin(version, 'picassoQ', 'q/dist/picasso-q.js', (picassoQ) => {
      picasso.use(picassoQ);

      loadPlugin(version, 'picassoHammer', 'hammer/dist/picasso-hammer.js', (picassoHammer) => {
        picasso.use(picassoHammer);

        if (callback) {
          callback();
        }
      });
    });
  }
}



