import lifecycle from './lifecycle';
import getURL from './misc/http';

/**
 * Handle initalization
 */
lifecycle.on('init', () => {
  // Enable sync
  const hostname = window.location.href.split('/')[2];
  let srvConfig;

  // Fetch the server config from this server
  getURL(`http://${hostname}/config`, (result) => {
    try {
      srvConfig = JSON.parse(result);
    } catch (e) {
      srvConfig = {};
    }

    lifecycle.trigger('srv-config', srvConfig);
  });
});



