import lifecycle from '../core/lifecycle';
import getURL from '../core/misc/http';
import loadScript from '../core/loadscript';

const head = document.getElementsByTagName('head')[0];
const scripts = [];
let availableVersions = [];

export function clearScripts() {
  scripts.forEach((script) => {
    try {
      head.removeChild(script);
    } catch (e) {
      // ignore
    }
  });
}

export function loadPicasso(version, callback) {
  clearScripts();
  delete window.picasso;

  const script = loadScript(`../picasso/${version}/picasso.js`, () => {
    if (callback) {
      callback(window.picasso, version);
    } else {
      lifecycle.trigger('picassoload', window.picasso, version);
    }
  });

  scripts.push(script);
}

export function loadPlugin(version, globalKey, path, callback) {
  delete window[globalKey];

  const script = loadScript(`../picasso/${version}/plugins/${path}`, () => {
    if (callback) {
      callback(window[globalKey], version);
    }
  });

  scripts.push(script);
}

function populateList(id, arr) {
  const sel = document.querySelector(id);

  if (sel) {
    sel.innerHTML = '';
    sel.className = '';

    const opt = document.createElement('option');
    opt.setAttribute('value', -1);
    opt.textContent = '<Switch version>';
    sel.appendChild(opt);

    arr.forEach((a) => {
      const elm = document.createElement('option');
      elm.setAttribute('value', a);
      elm.textContent = a;
      sel.appendChild(elm);
    });
  }
}

lifecycle.on('srv-config', (srvConfig) => {
  let hostname = window.location.href.split('/')[2];

  // Fix the hostname according to the server config
  /* if (srvConfig.hostname) {
    hostname = srvConfig.hostname + (srvConfig.port === 80 ? '' : `:${srvConfig.port}`);
  } */

  // Fetch the server config from this server
  getURL(`http://${hostname}/versions`, (result) => {
    try {
      availableVersions = JSON.parse(result);
    } catch (e) {
      availableVersions = [];
    }

    if (availableVersions) {
      populateList('#picassoversion', availableVersions);

      const select = document.querySelector('select#picassoversion');

      if (select) {
        select.addEventListener('change', function picSelectChange() {
          if (this.value !== '-1') {
            loadPicasso(this.value);
          }
        });
      }
    }
  });
});



