/* eslint no-mixed-operators: 0 */

import extend from 'extend';

export function subscribe(channel = 'default', hostname = undefined) {
  if (hostname === undefined) {
    hostname = window.location.href.split('/')[2];
  }

  const wsURL = `ws://${hostname}/sync`;

  const ws = new WebSocket(wsURL);
  const queue = [];

  const sync = function (args) {
    sync.sendRaw('sync', args);
  };

  sync.channel = channel;
  sync.hostname = hostname;

  sync.sendRaw = function (type, msg = '') {
    msg = extend(true, {}, msg);

    if (msg.data) {
      msg.data.qObject = undefined;
    }

    const payload = JSON.stringify({ channel, type, msg });
    try {
      ws.send(payload);
    } catch (e) {
      queue.push({ type, msg });
    }
  };

  sync.close = function () {
    ws.close();
  };

  ws.onmessage = function (msg) {
    msg = JSON.parse(msg.data);

    if (sync.onmessage && typeof sync.onmessage === 'function') {
      sync.onmessage(msg);
    }
  };

  ws.onopen = function () {
    sync.sendRaw('subscribe', { appVersion: navigator.appVersion || '', platform: navigator.platform || '', userAgent: navigator.userAgent || '' });

    queue.forEach((msg) => {
      sync.sendRaw(msg.type, msg.msg);
    });
  };

  return sync;
}

export function generateChannelName() {
  /* let items = [
    "Alpha", "Bravo", "Charlie", "Delta", "Echo", "Foxtrot", "Golf", "Hotel", "India",
    "Juliet", "Kilo", "Lima", "Mike", "November", "Oscar", "Papa", "Quebec", "Romeo",
    "Sierra", "Tango", "Uniform", "Victor", "Whiskey", "XRay", "Yankee", "Zulu"
  ];*/

  const alphabet = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'z'];

  const randomArrItem = function (arr) {
    return arr[Math.floor(Math.random() * arr.length)];
  };

  const output = randomArrItem(alphabet) + randomArrItem(alphabet) + Math.floor(1 + Math.random() * 99);

  return output.toLowerCase();
}



