import { qs, qsAll } from './queryselect';

export function onWindowEvent(event, fn) {
  window.addEventListener(event, fn);
}

export function onEvent(selector, event, fn, attach) {
  if (attach) {
    const item = qs(selector);
    item.addEventListener(event, fn);
  } else {
    window.addEventListener(event, (item) => {
      const matches = qsAll(selector);
      for (let i = 0; i < matches.length; i++) {
        if (matches[i] === item.target) {
          fn.call(matches[i], item);
        }
      }
    });
  }
}



